  background-color: #ffffff;
  color: #103AAE;
}
.btn--on-dark.focus-visible:active {
  background-color: #ffffff;
  color: #102654;
}
.btn--on-dark:active {
  background-color: #ffffff;
  color: #102654;
}
.btn--on-dark:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn--on-dark-disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn--on-light {
  background-color: transparent;
  color: #102654;
  border: 2px solid #102654;
}
@media (hover: hover) {
  .btn--on-light:hover:not(.focus-visible) {
    background: linear-gradient(90deg, #ED0233 0%, #003EB7 100%);
