  width: 100%;
  height: 2px;
  bottom: 0;
  transform: translateY(18px);
  left: 0;
  background-color: #D10A42;
}

.sub__tabs-element {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 40px;
  grid-row-gap: 40px;
}

.card {
  background: #ffffff;
  padding: 7px 7px;
}
.card:hover {
  background: linear-gradient(135deg, #D10A42 0%, #0041CA 100%);
}
.card:hover .card__btn {
  background: linear-gradient(90deg, #ED0233 0%, #003EB7 100%);
  color: #FFFFFF;
  border: none;
  outline: none;
  padding-top: 27px;
  padding-bottom: 21px;
  padding-left: 24px;
  padding-right: 24px;
}

.card__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 61px 10px;
  background-color: #ffffff;
}

.card__title {
  margin: 0;
  margin-bottom: 16px;
  text-align: center;
  font-size: 26px;
  font-weight: 700;
  line-height: 110.16%;
}
@media (max-width: 767px) {
  .card__title {
    margin-bottom: 20px;
    font-size: 22px;
  }
}

.card__descr {
  margin: 0;
  margin-bottom: 96px;
  text-align: center;
  font-size: 16px;
  line-height: 150%;
}
@media (max-width: 767px) {
  .card__descr {
    margin-bottom: 81px;
  }
}

.card__price {
  margin: 0;
  margin-bottom: 29px;
  position: relative;
  z-index: 1;
}
@media (max-width: 767px) {
  .card__price {
    margin-bottom: 27px;
  }
}

.card__price-count {
  display: block;
  padding-right: 44px;
  text-align: right;
  font-size: 60px;
  font-weight: 700;
  line-height: 150%;
  position: relative;
}
.card__price-count::after {
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 30px;
  height: 42px;
  background-image: url("../../img/svg/rouble.svg");
}

.card__price-bg {
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
  color: #E8E8E8;
  font-size: 100px;
  font-weight: 700;
  line-height: 150%;
  z-index: -1;
}

.card__btn {
  width: 206px;
  padding-top: 25px;
  padding-bottom: 20px;
}

.games {
  background-color: #ffffff;
  padding-top: 120px;
}
@media (max-width: 1365px) {
  .games {
    padding-top: 100px;
  }
}
@media (max-width: 767px) {
  .games {
    padding-top: 70px;
  }
}

.games__wrapper {
  display: grid;
  grid-template-columns: 43.1% 56.9%;
  justify-content: center;
}
@media (max-width: 767px) {
  .games__wrapper {
    display: flex;
    flex-direction: column;
  }
}

.games__col--left {
  background-color: #ED0233;
  padding-top: 85px;
  padding-bottom: 74px;
  padding-left: 78px;
  padding-right: 78px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, auto);
  grid-column-gap: 50px;
}
@media (max-width: 1365px) {
  .games__col--left {
    padding-top: 51px;
    padding-bottom: 51px;
    padding-left: 45px;
    padding-right: 45px;
    grid-column-gap: 29px;
  }
}
.games__col--right img {
  width: 100%;
  height: 500px;
  object-fit: cover;
}
@media (max-width: 1365px) {
  .games__col--right img {
    height: 292px;
  }
}

.games__title {
  grid-area: 1/1/3/2;
  margin: 0;
  margin-bottom: 35px;
  color: #FFFFFF;
  text-align: center;
  font-family: "AlumniSans", "Arial", sans-serif;
  font-size: 82px;
  font-weight: 600;
  line-height: 96%;
  letter-spacing: 2.46px;
}
@media (max-width: 1365px) {
  .games__title {
    margin-bottom: 15px;
    font-size: 48px;
    letter-spacing: 1.44px;
  }
}

.games__inner {
  grid-area: 1/2/3/3;
  display: flex;
  flex-direction: column;
}

.games__label {
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 400;
  line-height: 150%;
}
@media (max-width: 1365px) {
  .games__label {
    font-size: 12px;
  }
}

.games__date {
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 32px;
  max-width: 64px;
}
@media (max-width: 1365px) {
  .games__date {
    margin-bottom: 14px;
    max-width: 42px;
  }
}

.games__inner-text {
  color: #FFFFFF;
  font-size: 40px;
  font-weight: 700;
  line-height: 150%;
  border-top: 1px solid #ffffff;
}
.games__inner-text--date {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1365px) {
  .games__inner-text {
    font-size: 24px;
  }
}

.games__date-number {
  margin-right: 7px;
}
@media (max-width: 1365px) {
  .games__date-number {
    margin-right: 3px;
  }
}

.games__date-month,
.games__date-year {
  color: #FFFFFF;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
}
@media (max-width: 1365px) {
  .games__date-month,
.games__date-year {
    font-size: 10px;
  }
}

.games__date-inner {
  margin: 0;
  padding-top: 10px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1365px) {
  .games__date-inner {
    padding-top: 7px;
  }
}

.games__time {
  display: inline-flex;
  flex-direction: column;
  max-width: 88px;
}
@media (max-width: 1365px) {
  .games__time {
    max-width: 56px;
  }
}

.games__descr {
  grid-area: 3/1/4/3;
  margin: 0;
  margin-bottom: 53px;
  color: #FFFFFF;
  font-size: 18px;
  line-height: 150%;
}
@media (max-width: 1365px) {
  .games__descr {
    margin-bottom: 34px;
    font-size: 12px;
  }
}

.games__link {
  grid-area: 4/1/5/3;
}

.page-footer {
  background-color: #030A1A;
  padding-top: 41px;
  padding-bottom: 32px;
}
@media (max-width: 1365px) {
  .page-footer {
    padding-top: 42px;
    padding-bottom: 44px;
  }
}
@media (max-width: 767px) {
  .page-footer {
    padding-top: 53px;
    padding-bottom: 52px;
  }
}

.page-footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .page-footer__wrapper {
    position: relative;
  }
}

@media (max-width: 767px) {
  .page-footer__logo {
    position: absolute;
    top: -3px;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 40px;
  max-width: 440px;
}
@media (max-width: 1365px) {
  .nav__list {
    column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .nav__list {
    display: grid;
    grid-template-columns: repeat(2, 130px);
    grid-template-rows: repeat(3, auto);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
    margin-bottom: 40px;
  }
}

@media (max-width: 767px) {
  .nav__item--services {
    grid-area: 2/1/3/2;
  }
}
@media (max-width: 767px) {
  .nav__item--subs {
    grid-area: 3/1/4/2;
  }
}
@media (max-width: 767px) {
  .nav__item--actions {
    grid-area: 1/2/2/3;
  }
}
@media (max-width: 767px) {
  .nav__item--trainers {
    grid-area: 2/2/3/3;
  }
}
@media (max-width: 767px) {
  .nav__item--schedule {
    grid-area: 3/2/4/3;
  }
}

.nav__link {
  color: #FFFFFF;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
}
@media (max-width: 767px) {
  .nav__link {
    font-size: 16px;
  }
}

.social {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 11px;
  row-gap: 10px;
  max-width: 100px;
}
@media (max-width: 767px) {
  .social {
    width: 100%;
    max-width: 100%;
  }
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  border-radius: 50%;
}

